import React, {useState, useEffect} from 'react';
import './App.css';
import potter from './img/potter.png';
import firebase from './firebase';

const CANVAS_HEIGHT = 750;
const CANVAS_WIDTH = 1500;
// const GRAVITY = 0.125;
const GRAVITY = 0.2;
const OBSTACLE_WIDTH = 40;
const playerX = 200; //0.2*CANVAS_WIDTH;

let SPEED = 12;
let ANSWER = 1;
let SCORE = 0;
let SHOW_RESULTS = false;

function randomNum(a, b) {
    return Math.floor(Math.random() * b) + a; 
}

class Game {
    constructor() {
        this.gameState = 0;
    }
}

class Player {
    constructor() {
        this.width = 70;
        this.height = 70;
        this.position = {
            x: playerX,
            y: 0.5*CANVAS_HEIGHT
        }
        this.transform = {
            x: playerX,
            y: 0.5*CANVAS_HEIGHT
        }
        this.gravityDeceleration = 0;
        this.jumpBoost = 0;
        this.stop = 0;
        this.rotation = 0;
        this.direction = 1;
        this.x1 = 0;
        this.y1 = 0;
        this.h1 = 0;
        this.k1 = 0;
    }

    draw(ctx) {
        ctx.save();
        
        let upTilt = -50;
        let downTilt = 80;

        this.rotation += this.direction;
        this.direction += 0.5;
        if (this.direction > 1) {
            this.direction = 1;
        }

        if (this.rotation > downTilt) {
            this.rotation = downTilt;
        }
        if (this.rotation < upTilt) {
            this.rotation = upTilt;
        }

        var sin = Math.sin(this.rotation * Math.PI / 180);
        var cos = Math.cos(this.rotation * Math.PI / 180);

        let buffer = 40;
        this.y1 = buffer;
        this.h1 = this.height*Math.cos(this.rotation * Math.PI/180) + this.width*Math.sin(this.rotation * Math.PI/180) - buffer;
        this.x1 = this.height*Math.cos((90 - this.rotation) * Math.PI/180) - buffer;
        this.k1 = this.width*Math.cos(this.rotation * Math.PI/180) + this.height*Math.sin(this.rotation * Math.PI/180) - buffer;
        if (this.rotation < 0) {
            this.y1 = this.width*Math.cos((90 - this.rotation) * Math.PI/180) + buffer;
            this.h1 = this.height*Math.cos(this.rotation * Math.PI/180) - this.width*Math.sin(this.rotation * Math.PI/180) - 1.5*buffer;
            this.x1 = -buffer;
            this.k1 = this.width*Math.cos(this.rotation * Math.PI/180) - this.height*Math.sin(this.rotation * Math.PI/180) - 1.5*buffer;
        }
        this.position.x = this.transform.x - this.x1 - 50;
        this.position.y = this.transform.y + this.y1;
        // ctx.fillRect(this.position.x, this.position.y, this.k1, this.h1); // hit detection

        ctx.transform(cos, sin, -sin, cos, this.transform.x - this.width*cos/2, this.transform.y);

        let imgSrc = document.getElementById("spriteID");
        ctx.drawImage(imgSrc, 0, 0, this.width, this.height);
        ctx.restore();
    }

    update(deltaTime) {
        this.gravityDeceleration += GRAVITY;
        this.transform.y += this.gravityDeceleration;
    }

    jump() {
        this.gravityDeceleration = -6;
        this.direction = -7;
    }

}

class Obstacle {
    constructor() {
        this.tops = [0, 10, 20, 30, 40];
        this.heights = [10, 10, 10, 10, 10];
        this.speed = 10;
        this.positionX = CANVAS_WIDTH *.8;        
        this.window = 200;
        this.correct = 1;
    }

    create() {
        this.tops[0] = 0;
        this.tops[1] = randomNum(CANVAS_HEIGHT*0.05, CANVAS_HEIGHT*0.2);
        this.tops[2] = this.tops[1] + this.window;
        this.tops[3] = this.tops[2] + randomNum(CANVAS_HEIGHT*0.1, CANVAS_HEIGHT*0.15);
        this.tops[4] = this.tops[3] + this.window;
        this.correct = 1 + 2*(randomNum(1, 2)-1); // 1 or 3
        
        this.answerCorrect = ANSWER * randomNum(2, 9);
        this.answerIncorrect = ANSWER * randomNum(2, 9) + randomNum(1, ANSWER-1);
    }

    draw(ctx) {
        for (let i = 0; i < this.tops.length; i++) {
            ctx.fillStyle = "rgb(100, 100, 100)";
            if (i === 1 || i === 3) {                
                ctx.fillStyle = "white";
                if (SHOW_RESULTS) {
                    ctx.fillStyle = "red";
                }
            }
            if (i === this.correct && SHOW_RESULTS) {
                ctx.fillStyle = "green";
            }
            if (i+1 < this.tops.length) {
                ctx.fillRect(this.positionX, this.tops[i], OBSTACLE_WIDTH, this.tops[i+1] - this.tops[i]);
            } else {
                ctx.fillRect(this.positionX, this.tops[i], OBSTACLE_WIDTH, CANVAS_HEIGHT - this.tops[i]);
            }

            // draw scores
            if (i === 1 || i === 3) { 
                ctx.fillStyle = "blue";
                ctx.font = "30px Verdana";
                if (i === this.correct) {
                    ctx.fillText(this.answerCorrect, this.positionX, this.tops[i] + this.window/2);
                } else {
                    ctx.fillText(this.answerIncorrect, this.positionX, this.tops[i] + this.window/2);
                }
            }
        }
    }

    update(deltaTime) {
        this.positionX -= SPEED;
        if (this.positionX + OBSTACLE_WIDTH < 0) {
            this.positionX = CANVAS_WIDTH;
            this.create();
        }
        SCORE += SPEED;
        document.getElementById("scoreID").innerHTML = "Score: " + SCORE;
    }
}


const Flappy = () => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");

    useEffect(() => {
        let savedUsername = sessionStorage.getItem("savedUsername");
        if (savedUsername) {
            showUserDetails(savedUsername)
        } else {
            document.getElementsByClassName("signIn")[0].style.display = "none";
            document.getElementsByClassName("signUp")[0].style.display = "block";
        }
    });

    async function showUserDetails(savedUsername) {
        document.getElementsByClassName("signIn")[0].style.display = "block";
        document.getElementsByClassName("signUp")[0].style.display = "none";
        document.getElementById("usernameID").innerHTML = "Welcome " + savedUsername;   
        let points = await firebase.getPoints(savedUsername);
        document.getElementById("pointsID").innerHTML = "You have " + points + " points";
    }
    
    async function collisionCheck(game, player, allObstacles, ctx) {
        if (player.transform.y > CANVAS_HEIGHT) {
            player.transform.y = CANVAS_HEIGHT;
            game.gameState = -1;
        }

        let passed = true;
        if (player.position.x + player.k1 > allObstacles.positionX
            && player.position.x < allObstacles.positionX + OBSTACLE_WIDTH) {
                passed = false;
                if (player.position.y > allObstacles.tops[allObstacles.correct]
                    && player.position.y + player.h1 < allObstacles.tops[allObstacles.correct] + allObstacles.window) {
                        passed = true;
                }
        }

        if (passed === false) {
            game.gameState = -1;
            SHOW_RESULTS = true;
            // document.getElementById("testID").innerHTML = "HIT";
            allObstacles.draw(ctx);
            player.draw(ctx);
            await updatePoints();
        } else {
            // document.getElementById("testID").innerHTML = "MISS";
        }
        
    }

    async function updatePoints() {
        let savedUsername = sessionStorage.getItem("savedUsername");
        if (savedUsername) {
            await firebase.updatePoints(savedUsername, SCORE);
        }
    }

    async function startGame() {
        console.log("game has begun, speed is " + SPEED);
        document.getElementById("gameScreen").width = CANVAS_WIDTH;
        document.getElementById("gameScreen").height = CANVAS_HEIGHT;
        document.getElementById("speedID").innerHTML = "Speed: " + SPEED;
        // let canvas = document.getElementById("gameScreen");
        // let ctx = canvas.getContext("2d");
        
        let game = new Game();
        game.gameState = 1;

        let btns = document.getElementsByClassName("speedBtn");
        for (let i = 0; i < btns.length; i++) {
            btns[i].disabled = true;
        }

        let player = new Player();
        // let player = new TestPlayer();

        document.addEventListener("keydown", event => {
            switch (event.key) {
                case " ":
                    //player.jumpBoost = 1;
                    player.jump();
                    break;
                default:
            }
        });

        let canvas = document.getElementById("gameScreen");
        canvas.addEventListener("click", function() { player.jump() });
        let ctx = canvas.getContext("2d");
        player.draw(ctx);

        // countdown delay
        for (let i = 3; i > 0; i--) {
            ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
            ctx.font = "60px Verdana";
            ctx.fillText("Multiples of " + ANSWER, CANVAS_WIDTH*0.3, CANVAS_HEIGHT/2);
            ctx.fillText(i, CANVAS_WIDTH*0.4, CANVAS_HEIGHT/2+100);
            // delay countdown
            await new Promise(resolve => {
                setTimeout(() => {
                resolve(2);
                }, 1000);
            });
        }
        
        let allObstacles = new Obstacle();
        allObstacles.create();

        requestAnimationFrame(
            function(timestamp) {
                gameLoop(timestamp, ctx, game, player, allObstacles);
            }
        );
    }

    function makeQuestion() {
        ANSWER = randomNum(2, 10);
    }

    let lastTime = 0;
    function gameLoop(timestamp, ctx, game, player, allObstacles) {
        if (game.gameState === 1) {

            let deltatime = timestamp - lastTime;
            lastTime = timestamp;
            ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT);
            ctx.font = "60px Verdana";
            ctx.fillText("Multiples of " + ANSWER, CANVAS_WIDTH*0.3, CANVAS_HEIGHT/2);

            allObstacles.update(deltatime);
            allObstacles.draw(ctx);

            player.update(deltatime);
            player.draw(ctx);

            requestAnimationFrame(
                function(timestamp) {
                    gameLoop(timestamp, ctx, game, player, allObstacles);
                }
            );

            collisionCheck(game, player, allObstacles, ctx);

        } else {
            console.log("game over");
            document.getElementById("resetBtn").style.display = "block";
            let btns = document.getElementsByClassName("speedBtn");
            for (let i = 0; i < btns.length; i++) {
                btns[i].disabled = false;
            }
        }
    }

    async function reset() {
        let savedUsername = sessionStorage.getItem("savedUsername");
        let points = await firebase.getPoints(savedUsername);
        document.getElementById("pointsID").innerHTML = "You have " + points + " points";
        SCORE = 0;
        console.log("reset");
        document.getElementById("resetBtn").style.display = "none";
        SHOW_RESULTS = false;
        makeQuestion();
        // setTimeout(startGame, 1500);
        startGame();
    }

    function decreaseSpeed() {
        SPEED--;
        if (SPEED < 0) {
            SPEED = 0;
        }
        document.getElementById("speedID").innerHTML = "Speed: " + SPEED;
    }

    function increaseSpeed() {
        SPEED++;
        if (SPEED > 20) {
            SPEED = 20;
        }
        document.getElementById("speedID").innerHTML = "Speed: " + SPEED;
    }

    function btnMsg(msg) {
        document.getElementById("formMsgID").innerHTML = msg;
        document.getElementById("formSubmitMsg").innerHTML = msg;
        toggleForm();
    }
    
    function toggleForm() {
        if (document.getElementById("loginFormID").style.display === "block") {
            document.getElementById("loginFormID").style.display = "none";
            document.getElementById("openLoginBtnID").style.display = "block";
            document.getElementById("openSignUpBtnID").style.display = "block";
        } else {
            document.getElementById("loginFormID").style.display = "block";
            document.getElementById("openLoginBtnID").style.display = "none";
            document.getElementById("openSignUpBtnID").style.display = "none";
        }
    }

    async function signUser() {
        // check if loggin in or signing up
        if (username.length >= 4) {
            if (password.length >= 4) {
                if (document.getElementById("formMsgID").innerHTML === "Login") {
                    // await firebase.loginUser()
                    console.log("logging in user");
                    loginUser(username, password);
                } else {
                    // sign up check
                    console.log("creating in user");
                    let result = await firebase.signUpUser(username, password);
                    if (result === false) {
                        alert("A username with that email already exists. Please use a different email");
                    } else {
                        loginUser(username, password);
                    }
                }
                console.log("'" + username + "'");
                console.log("'" + password + "'");
            } else {
                alert("Password must be at least 4 characters");
                setPassword("");
            }
        } else {
            alert("Username must be at least 4 characters");
        }
    }

    async function loginUser(username, password) {
        let result = await firebase.loginUser(username, password);
        if (result === true) {
            sessionStorage.setItem("savedUsername", username);
            toggleForm();
            await showUserDetails(username);
        } else {
            setPassword("");
            alert(result);
        }
    }

    function logout() {
        sessionStorage.clear();
        document.getElementsByClassName("signIn")[0].style.display = "none";
        document.getElementsByClassName("signUp")[0].style.display = "block";
    }

    return ( 
        <div className="Flappy">
            <br />
            <div className="signUp">
                <button id="openLoginBtnID" type="submit" className="btn btn-primary" onClick={() => btnMsg("Login")}>Login</button>
                <button id="openSignUpBtnID" type="submit" className="btn btn-success" onClick={() => btnMsg("Create User")}>Create Account</button>
                <form id="loginFormID" style={{display:"none"}} onSubmit={signUser}>
                    <h4 id="formMsgID">Login</h4>
                    <div className="form-group">
                        <label>Username</label>
                        <input type="text" required value={username} onChange={(e) => setUsername(e.target.value)} className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Username" />
                    </div>
                    <div className="form-group">
                        <label>Password</label>
                        <input type="password" required value={password} onChange={(e) => setPassword(e.target.value)} className="form-control" id="exampleInputPassword1" placeholder="Password" />
                    </div>
                    <button id="formSubmitMsg" type="submit" className="btn btn-primary">Login</button>
                    <button className="btn btn-secondary" onClick={toggleForm}>Hide</button>
                </form>
            </div>
            
            <h4 id="scoreID" style={{display: "inline"}}>Score: 0</h4>
            <br />
            <canvas id="gameScreen"  height="800" width="800"></canvas>
            <br />

            <table>
                <tbody>
                    <tr>
                        <td>
                            <div className="signIn" style={{display:"none"}}>
                                <h4 id="usernameID"> </h4>
                                <p id="pointsID"> </p>
                                <button className="btn btn-secondary" onClick={logout}>Logout</button>
                            </div>
                        </td>
                        <td>
                            <button id="resetBtn" className="btn btn-light" onClick={reset}>Play Again?</button>
                            <p id="testID"></p>
                            <button className="speedBtn btn btn-light" onClick={decreaseSpeed} style={{display: "inline"}}>-</button>
                            <p id="speedID" style={{display: "inline"}}>Speed:</p>
                            <button className="speedBtn btn btn-light" onClick={increaseSpeed} style={{display: "inline"}}>+</button>
                        </td>
                    </tr>
                </tbody>
            </table>

            <img id="spriteID" alt="potter" src={potter} style={{display:"none"}}></img>
        </div>
    );
}

export default Flappy;